import React from 'react'
import Link from 'gatsby-link'

const IndexPage = ({data}) => {
  const { edges: posts } = data.allMarkdownRemark

  return (
    <div className='flex flex-column justify-center items-center'>
      <h1 className='f3 f2-ns fw7 lh-title mt0 mb3 avenir'>Hi, I'm Taylor Bell.</h1>
      <h4 className='f4 f3-ns fw4 lh-title mt0 avenir'>I'm a researcher &amp; developer that is interested in the interesting.</h4>

      <p className='f4 f3-ns fw4 lh-title mt0 avenir'>Here are some things I've written:</p>

      <div className='flex flex-column justify-center items-center'>
        {posts
          .map(({node: post}) => {
            return (
              <div className='blog-post-preview w-100' key={post.id}>
                <h1 className='avenir f3 f2-ns fw3 mb1 pb2'>
                  <Link to={post.frontmatter.path}
                    className='dark-gray no-underline dim'
                    style={{borderBottom: '3px solid rgba(0,0,0,.05)'}}
                  >
                    {post.frontmatter.title}
                  </Link>
                </h1>
                {/* <p className='avenir mt0 moon-gray'>{post.frontmatter.date}</p> */}
                <p className='avenir'>{post.frontmatter.excerpt}</p>
                <ul className='moon-gray'>
                  {post.frontmatter.tags.sort().map((tag, i) => {
                    return (
                      <li key={i} className='dim'>
                        <Link to={`/tags/${tag}`} className='avenir dark-gray o-70'>
                          {tag}
                        </Link>
                      </li>
                    )
                  })}
                </ul>
              </div>
            )
          })
          }
      </div>
    </div>
  )
}

export const pageQuery = graphql`
  query IndexQuery {
      allMarkdownRemark(sort:{fields:[frontmatter___date],
    								order: DESC},
                    ) {
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "MMMM DD, YYYY")
            path
            tags
            excerpt
          }
        }
      }
    }
  }
`

export default IndexPage



